$herocolor: #FFD32E;
$textcolor: #00103E;
$basiclight: #F5F5F5;
$basicdark: #00103E;
/*$basicdark: #161624; (gray)*/


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, strong {
  font-family: 'Merriweather', serif;
  line-height: 1.2em;
}

p, li, span, label, input, textarea {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6em;
}

a {
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: $basicdark;
  font-size: .8rem;

  &:hover {
    text-decoration: underline;
  }
}

ul {
  list-style: none;
}

hr {
  margin: 10px auto;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}

/*text sizes*/
h1 {font-size: 3.5rem;}
h2 {font-size: 3rem;}
h3 {font-size: 2.5rem;}
h4 {font-size: 2rem;}
h5 {font-size: 1rem;}
h6 {font-size: .8rem;}

.text-center {text-align: center;}

.lead {font-size: 20px;}
.xs {font-size: .8rem;}
.sm {font-size: 1rem;}
.md {font-size: 1.5rem;}
.lg {font-size: 3rem;}
.xl {font-size: 4rem;}

/*margins*/
.my-1 {margin: 1rem 0;}
.my-2 {margin: 1.5rem 0;}
.my-3 {margin: 2rem 0;}
.my-4 {margin: 3rem 0;}
.my-5 {margin: 4rem 0;}

.m-1 {margin: 1rem;}
.m-2 {margin: 1.5rem;}
.m-3 {margin: 2rem;}
.m-4 {margin: 3rem;}
.m-5 {margin: 4rem;}

/*paddings*/

.py-1 {padding: 1rem 0;}
.py-2 {padding: 1.5rem 0;}
.py-3 {padding: 2rem 0;}
.py-4 {padding: 3rem 0;}
.py-5 {padding: 4rem 0;}

.p-1 {padding: 1rem;}
.p-2 {padding: 1.5rem;}
.p-3 {padding: 2rem;}
.p-4 {padding: 3rem;}
.p-5 {padding: 4rem;}
